.Card {
    border: 3px solid black;
    border-radius: 10px;
    background-color: white;

    margin: 10px 250px;
    height: 200px;

    display: flex;
    flex-direction: row;
    flex: 1;
}
.foto {
    height: 150px;
    width: 150px;
}

.Card .Image{
    margin: 25px;
    background-color: black;
    color: white;
    align-items: center;
    justify-content: space-around;
    display: flex;
    width: 150px;
    height: 150px;
}

.Card .Descricao {
    margin: 25px;
    /* background-color: yellow; */
    color: gray;
    display: flex;
    flex-direction: column;
    flex: 1;
}
.Card .Cima {
    /* background-color: lightblue; */
    color: gray;

    justify-content: space-between;
    
    display: flex;
    flex: 1;
}

.Card .Baixo {
    /* background-color: lightblue; */
    color: gray;
    justify-content: space-between;
    display: flex;
    margin: 0 50px
}
